<template>
 <div id="view-employee">
  <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="collection-item">
        <table>
          <tr>
            <td class="grey-text">Employee ID:</td>
            <td class="right-align">{{employee_id}}</td>
          </tr>
        </table>
      </li>
      <li class="collection-item">
        <table>
          <tr>
            <td class="grey-text">Department:</td>
            <td class="right-align">{{dept}}</td>
          </tr>
        </table>
      </li>
      <li class="collection-item">
        <table>
          <tr>
            <td class="grey-text">Position:</td>
            <td class="right-align">{{position}}</td>
          </tr>
        </table>
      </li>
  </ul>
  <!-- buttons -->
  <router-link to="/" class="btn green">Back</router-link>
  <button @click="deleteEmployee" class="btn red right">Delete</button>

  <div class="fixed-action-btn">
   <router-link v-bind:to="{name: 'edit-employee', params: {employee_id: employee_id}}" class="btn-floating btn-large red"><i class="fa fa-pencil"></i>

   </router-link>
 </div>
 </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'view-employee',
  data () {
    return {
      emplyee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter (to, from, next) {
    db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        next(vm => {
          vm.employee_id = doc.data().employee_id
          vm.name = doc.data().name
          vm.dept = doc.data().dept
          vm.position = doc.data().position
        })
      })
    })
  },
  watch: {
    '$route': 'fetchData'
  },
  methods: {
    fetchData () {
      db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          this.employee_id = doc.data().employee_id
          this.name = doc.data().name
          this.dept = doc.data().dept
          this.position = doc.data().position
        })
      })
    },
    deleteEmployee () {
      if(confirm('Are you sure?')) {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapshot => {
        querySnapshot.forEach(doc => {
          doc.ref.delete()
          this.$router.push('/')
        })
      })
      }
    }
  }
}
</script>


